import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isFetchContainer: false, // 
    isUpload: false, // 
    crateData: {}, // 8
    changeCrateDataNum: 0, // changeCrateData
    edition: 'v21.7.8', // 
    newEdition: '', // 
    code:1
  },
  mutations: {
    changeIsFetchContainer (obj, data) { // isFetchContainer
      this.state.isFetchContainer = data
    },
    changeIsUpload (obj, data) { // isUpload
      this.state.isUpload = data
      // console.log(this.state.isUpload)
    },
    // 
    changeCrateData (obj, data) {
      if (data) { // crateData
        this.state.crateData[data[0]] = data[1]
        this.state.changeCrateDataNum++
        // console.log('', this.state.crateData, this.state.changeCrateDataNum)
      } else { // data
        this.state.crateData = {}
        this.state.changeCrateDataNum = 0
        // console.log('', this.state.crateData, this.state.changeCrateDataNum)
      }
    },
    // newedion
    changeNewEdition (obj, data) {
      this.state.newEdition = data
    }
  },
  actions: {
  },
  modules: {
  }
})
