import http from '@/utils/http'

// post
/*
  parseurltimeoutmethoddocker
  funres.Code===200 /docker Code
  tahtthis
  
  const docker = 'curl --unix-socket /var/run/docker.sock -s "http:/v1.40/info'
  post({ docker }, (res) => {
    console.log(res)
  }, this)
*/
const post = (parse, fun, that, data) => {
  if (!parse.url) {
    parse.url = '/docker'
  }
  if (!parse.timeout) {
    parse.timeout = 600000
  }
  if (!parse.method) {
    parse.method = 'POST'
  }
  http({
    url: parse.url,
    method: parse.method,
    timeout: parse.timeout,
    data: parse.docker ?  {
      docker: parse.docker,
      ...data
    } : {
      ...data
    }
  }).then(res => {
    if (res.Code === 200) {
      !res.Data && (res.Data = '{}')
      fun(JSON.parse(res.Data))
    } else if (res.Code === 401) {
      that.$message({
        showClose: true,
        message: '',
        type: 'error'
      })
      that.$router.push('/Login')
    } else {
      let str = ''
      if (res.Msg) {
        str = res.Msg
      }
      that.$message({
        showClose: true,
        message: str,
        type: 'error'
      })
    }
  }).catch((err) => {
    that.$message({
      showClose: true,
      message: err,
      type: 'error'
    })
  })
}

export default post
